@font-face {
  font-family: "Montserrat";
  src: url(../font/Montserrat/Montserrat-VariableFont_wght.ttf);
  font-weight: 100 900;
  font-style: normal;
}
@font-face {
  font-family: "Open Sans";
  src: url(../font/OpenSans/OpenSans-VariableFont_wdth\wght.ttf);
  font-weight: 100 900;
  font-style: normal;
}
$lightwght: 300;
$regularwght: 400;
$boldwght: 700;
$xtraboldwght: 900;

@mixin flex-width {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  margin-top: 3rem;
}

@mixin header-format {
  margin: 0 auto;
  font-weight: $boldwght;

}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  -ms-overflow-style: none;
  scrollbar-width: none;
  font-size: 20px;
  font-weight: $regularwght;
  font-family: "Montserrat", Helvetica, sans-serif;
  color: #f3f4f6;
}

h1 {
  font-size: 5rem;
}
h2 {
  font-size: 3.1rem;
}
h3 {
  font-size: 1.91rem;
}
h4 {
  font-size: 1.19rem;
}
h5 {
  font-size: 0.75rem;
}

body {
  background-color: black;
}

// NAV SECTION -------
.nav {
  z-index: 1;
  height: 3rem;
  width: 100%;
  background-color: black;
}

.navlinks {
  list-style-type: none;
  display: none;
  justify-content: center;
  align-items: center;
  height: inherit;

  li {
    margin-right: 3rem;

    a {
      text-decoration: none;
      color: white;
      font-weight: $lightwght;
      position: relative;
      padding: 4px 0;
    }
    &:last-of-type {
      margin-right: 0;
    }

    a:is(:link, :active, :visited).active {
      &:before {

        content: "";
        position: absolute;
        display: block;      
        background-color: white;

        width: 110%;
        height: 2px;
        bottom: 0;
        left: -5%
      }
    }

  }
}

//HEADER SECTION ------

header {
  @include flex-width();
  h3 {
    @include header-format();
  }
}

// HOME SECTION ------
.home {
  @include flex-width();
  margin-top: 10rem;
}

.intromsg {
  display: block;
  padding-left: 1rem;
  padding-right: 1rem;
  width: 100%;
  user-select: none;
  h1 {
    font-weight: $xtraboldwght;
    line-height: 4.2rem;
    padding-bottom: 1rem;
  }
  h3 {
    font-family: "Open Sans", Arial, sans-serif;
  }
  h4 {
    font-family: "Open Sans", Arial, sans-serif;
    font-weight: $regularwght;
    font-size: 1rem;
    width: 100%;
    span {
      color: rgb(37, 230, 237);
    }
    transition: font-weight 0.4s;
    &:hover {
      font-weight: $boldwght;
    }
  }
}

// UIUX SECTION ------
.uix {
  @include flex-width();

  .uix-content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  
    img {
      max-width: 90%;
      height: auto;
      border-radius: 18px;
      margin-top: 2rem;
      transition: all 0.5s;
  
      &:last-of-type {
        margin-bottom: 2rem;
      }
  
      &:hover {
        transform: scale(1.02);
      }
    }
  }
}

//GRAPHIC DESIGN SECTION ------

.graphicdesign {
  @include flex-width();
}

//MEDIA QUERY ------ RESPONSIVE SCREEN
@media only screen and (min-width: 768px) {
  .navlinks {
    display: flex;
  }

  .intromsg {
    width: 75%;
    padding-left: 5rem;
    padding-right: 5rem;
  }

  .uix-content {
    img {
      max-width: 60%;
    }
  }
}
